import React from 'react'
import { Link } from 'react-router-dom'
import './styles.css'

class Button extends React.Component {
  constructor(props) {
    super(props)
  }

  render() {
    return (
      <Link to={this.props.path} className="button">
        {this.props.children}
      </Link>
    )
  }
}

export default Button
